* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

[class*=grid] {
  margin: 0;
}


/* Re-usable Utiity Classes  */
.tall-50 {
  min-height: 50vh;
}

.tall-60 {
  min-height: 60vh;
}

.tall-70 {
  min-height: 70vh;
}

.tall-80 {
  min-height: 80vh;
}

.tall-90 {
  min-height: 90vh;
}

.padded {
  padding: 5%;
}

.padded-1 {
  padding: 1rem;
}

.padded-2 {
  padding: 2rem;
}

.padded-t-2 {
  padding-top: 2rem;
}

.padded-b-2 {
  padding-bottom: 2rem;
}

.margin-b-0 {
  margin-bottom: 0;
}

.bg-featured {
  background-color: #183039;
}

.bg-dark {
  background-color: #24282B;
}

.light-text {
  color: white;
}

.featured-text {
  color: #183039;
}

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}


/* General typography & universal styles  */
body {
  font-family: 'Martel', serif;
  font-weight: 200;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-family: 'Didot', Georgia, serif;
}

h1 {
  font-size: 10rem;
  line-height: 0.8;
}

h1 span {
  display: block;
  font-size: 3.5rem;
  line-height: 0.9;
}

h2 {
  font-size: 2.5rem;
}

h2 span {
  display: block;
  font-size: 2rem;
  line-height: 0.9;
}

h3 {
  font-size: 2.5rem;
}

p {
  font-weight: 200;
}

a {
  color: inherit;
  text-decoration: none;
}


/* Unique typography styles  */
.tight-text {
  line-height: 0.8;
}

.price {
  display: block;
  font-weight: 400;
  margin-top: 0.5rem;
  text-align: right;
}

.text-lg {
  font-size: 4.5rem;
}

.text-xl {
  font-size: 6rem;
}

.menu p {
  margin-top: 0.5rem;
  font-size: 0.9rem;
}

.menu h3 {
  margin: 1rem 0;
}


/* Header & Footer */
footer {
  padding: 5%;
  padding-bottom: 1rem;
}

footer a {
  font-size: 1.2rem;
}

footer h2 {
  margin-bottom: 0.5rem;
}

.sub-footer {
  padding-top: 5%;
}

.sub-footer p {
  font-size: 0.8rem;
  opacity: 0.7;
}


/* Images & Background Images */
.bg-image {
  background-color: #183039;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.bg-glass {
  background-image: url('./images/negroni-glass-faded.jpg')
}

.bg-window {
  background-image: url('./images/negroni-2.jpg')
}

.bg-plant {
  background-image: url('./images/negroni-4.jpg')
}

.bg-water {
  background-image: url('./images/water-light.jpg')
}


/* Gridlex SM  */
@media(max-width: 48em) {
  .mob-hide {
    display: none;
  }

  .mob-padded-t-0 {
    padding-top: 0;
  }

  h1 {
    font-size: 6rem;
  }

  h1 span {
    font-size: 2rem;
  }

  footer a {
    font-size: 1rem;
  }
}

/* API Section */
.paragraphApi {
  margin-top: 3rem;
  text-align: center;
}

.apiContainer {
  background-color: #29505d;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

button {
  color: #29505d;
  font-weight: bold;
  width: 100px;
  border-radius: 10px;
  background-color: white;
  border: #183039 solid 2px;
  height: 40px;
  margin-bottom: 10px;
  margin-top: 20px;
}

button:hover {
  cursor: pointer;
  color: white;
  background-color: #183039;
}



input {
  font-family: 'Martel', serif;
  height: 40px;
  width: 200px;
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 200;
  text-align: center;
  border-radius: 10px;
  border: #183039 solid 2px;
  font-size: 1rem;
}

.name {
  margin-top: 20px;
  margin-bottom: 30px;
  text-align: justify;
  font-size: 1.8rem;
}

.instructions {
  /*margin-top: 10px;*/
  margin-left: 15%;
  margin-right: 15%;
  margin-bottom: 1%;
  text-align: justify;

}

/* Custom Media Query XXS  */
@media(max-width: 25em) {
  h1 {
    font-size: 4.5rem;
  }

  h1 span {
    font-size: 1.5rem;
  }
}